import { Injectable } from '@angular/core';
import { CronConfig, Base } from './export-schedules.interface';
import { TranslateService } from '@ngx-translate/core';
import { formatDate } from '@angular/common';
import { gettext } from '@c8y/ngx-components';

@Injectable()
export class CronService {
  daysOfWeekPosix: any[] = [];
  daysOfMonth = [
    { value: '1', label: '1.' },
    { value: '2', label: '2.' },
    { value: '3', label: '3.' },
    { value: '4', label: '4.' },
    { value: '5', label: '5.' },
    { value: '6', label: '6.' },
    { value: '7', label: '7.' },
    { value: '8', label: '8.' },
    { value: '9', label: '9.' },
    { value: '10', label: '10.' },
    { value: '11', label: '11.' },
    { value: '12', label: '12.' },
    { value: '13', label: '13.' },
    { value: '14', label: '14.' },
    { value: '15', label: '15.' },
    { value: '16', label: '16.' },
    { value: '17', label: '17.' },
    { value: '18', label: '18.' },
    { value: '19', label: '19.' },
    { value: '20', label: '20.' },
    { value: '21', label: '21.' },
    { value: '22', label: '22.' },
    { value: '23', label: '23.' },
    { value: '24', label: '24.' },
    { value: '25', label: '25.' },
    { value: '26', label: '26.' },
    { value: '27', label: '27.' },
    { value: '28', label: '28.' },
    { value: '29', label: '29.' },
    { value: '30', label: '30.' },
    { value: '31', label: '31.' }
  ];
  months: any[] = [];
  hours: any[] = [];
  minutes: any[] = [];
  intervals = [
    { value: 2, label: gettext('Hour') },
    { value: 3, label: gettext('Day') },
    { value: 4, label: gettext('Week') },
    { value: 5, label: gettext('Month') },
    { value: 6, label: gettext('Year') }
  ];
  constructor(private translateService: TranslateService) {
    for (let x = 0; x < 24; x++) {
      this.hours.push({ value: x.toString(), label: `${x}` });
    }
    for (let x = 0; x < 60; x = x + 5) {
      this.minutes.push({ value: x.toString(), label: `${x}` });
    }

    for (let x = 0; x < 7; x++) {
      this.daysOfWeekPosix.push({
        value: x.toString(),
        label: this.getWeekDayName(({ weekday: x } as unknown) as CronConfig)
      });
    }

    for (let x = 1; x < 13; x++) {
      this.months.push({
        value: x.toString(),
        label: this.getMonthName(({ month: x } as unknown) as CronConfig)
      });
    }
  }

  generateCron(cronConfig: CronConfig) {
    let cron: string = '';
    cron = cronConfig.minute ? `${cronConfig.minute}` : '*';
    cron += cronConfig.hour ? ` ${cronConfig.hour}` : ' *';
    cron += cronConfig.day ? ` ${cronConfig.day}` : ' *';
    cron += cronConfig.month ? ` ${cronConfig.month}` : ' *';
    cron += cronConfig.weekday ? ` ${cronConfig.weekday}` : ' *';

    return cron;
  }

  generateCronConfig(cron: string) {
    const parts = cron.split(/\s+/);
    const cronConfig: CronConfig = {
      minute: parts[0],
      hour: parts[1],
      day: parts[2],
      month: parts[3],
      weekday: parts[4]
    };

    return cronConfig;
  }

  getBase(cronConfig: CronConfig) {
    let base: Base = Base.Initial;

    if (
      cronConfig.minute !== '*' &&
      cronConfig.hour === '*' &&
      cronConfig.day === '*' &&
      cronConfig.month === '*' &&
      cronConfig.weekday === '*'
    ) {
      base = Base.Hour;
    } else if (
      cronConfig.minute !== '*' &&
      cronConfig.hour !== '*' &&
      cronConfig.day === '*' &&
      cronConfig.month === '*' &&
      cronConfig.weekday === '*'
    ) {
      base = Base.Day;
    } else if (
      cronConfig.minute !== '*' &&
      cronConfig.hour !== '*' &&
      cronConfig.day === '*' &&
      cronConfig.month === '*' &&
      cronConfig.weekday !== '*'
    ) {
      base = Base.Week;
    } else if (
      cronConfig.minute !== '*' &&
      cronConfig.hour !== '*' &&
      cronConfig.day !== '*' &&
      cronConfig.month === '*' &&
      cronConfig.weekday === '*'
    ) {
      base = Base.Month;
    } else if (
      cronConfig.minute !== '*' &&
      cronConfig.hour !== '*' &&
      cronConfig.day !== '*' &&
      cronConfig.month !== '*' &&
      cronConfig.weekday === '*'
    ) {
      base = Base.Year;
    } else {
      // cronConfig invalid
    }

    return base;
  }

  validateModels(base: Base, cronConfig: CronConfig) {
    let valid: boolean;
    switch (base) {
      case Base.Initial: // Please select
        valid = false;
        break;
      case Base.Hour:
        if (cronConfig.minute !== '*') {
          valid = true;
        } else {
          valid = false;
        }
        break;
      case Base.Day:
        if (cronConfig.minute !== '*' && cronConfig.hour !== '*') {
          valid = true;
        } else {
          valid = false;
        }
        break;
      case Base.Week:
        if (cronConfig.minute !== '*' && cronConfig.hour !== '*' && cronConfig.weekday !== '*') {
          valid = true;
        } else {
          valid = false;
        }
        break;
      case Base.Month:
        if (cronConfig.minute !== '*' && cronConfig.hour !== '*' && cronConfig.day !== '*') {
          valid = true;
        } else {
          valid = false;
        }
        break;
      case Base.Year:
        if (
          cronConfig.minute !== '*' &&
          cronConfig.hour !== '*' &&
          cronConfig.day !== '*' &&
          cronConfig.month !== '*'
        ) {
          valid = true;
        } else {
          valid = false;
        }
        break;
      default:
        valid = false;
    }

    return valid;
  }

  clearNextModels(base: Base, cronConfig: CronConfig) {
    if (base === Base.Initial) {
      // please select, delete all
      // cron expression: every minute at second 0
      cronConfig.minute = '*';
      cronConfig.hour = '*';
      cronConfig.day = '*';
      cronConfig.month = '*';
      cronConfig.weekday = '*';
    } else if (base === Base.Hour) {
      // hour, don't delete minutes
      // cron expression: every hour, at whatever minute, at second 0
      cronConfig.minute = cronConfig.minute === '*' ? this.minutes[0].value : cronConfig.minute;
      cronConfig.hour = '*';
      cronConfig.day = '*';
      cronConfig.month = '*';
      cronConfig.weekday = '*';
    } else if (base === Base.Day) {
      // day, don't delete minutes and hours
      // cron expression: every day of every month, at whatever hour and minute, at second 0
      cronConfig.minute = cronConfig.minute === '*' ? this.minutes[0].value : cronConfig.minute;
      cronConfig.hour = cronConfig.hour === '*' ? this.hours[0].value : cronConfig.hour;
      cronConfig.day = '*';
      cronConfig.month = '*';
      cronConfig.weekday = '*';
    } else if (base === Base.Week) {
      // week, delete month and day
      // cron expression: every month, at whatever weekday, hour and minute, at second 0
      cronConfig.minute = cronConfig.minute === '*' ? this.minutes[0].value : cronConfig.minute;
      cronConfig.hour = cronConfig.hour === '*' ? this.hours[0].value : cronConfig.hour;
      cronConfig.day = '*';
      cronConfig.month = '*';
      cronConfig.weekday = cronConfig.weekday === '*' || cronConfig.weekday === '?' ? this.daysOfWeekPosix[0].value : cronConfig.weekday ;
    } else if (base === Base.Month) {
      // month, delete month and weekday
      // cron expression: every month, at whatever day of month, hour and minute, at second 0
      cronConfig.minute = cronConfig.minute === '*' ? this.minutes[0].value : cronConfig.minute;
      cronConfig.hour = cronConfig.hour === '*' ? this.hours[0].value : cronConfig.hour;
      cronConfig.day = cronConfig.day === '*' ? this.daysOfMonth[0].value : cronConfig.day;
      cronConfig.month = '*';
      cronConfig.weekday = '*';
    } else if (base === Base.Year) {
      // year, delete weekday
      // cron expression: every year, at whatever month, day of month, hour and minute, at second 0
      cronConfig.minute = cronConfig.minute === '*' ? this.minutes[0].value : cronConfig.minute;
      cronConfig.hour = cronConfig.hour === '*' ? this.hours[0].value : cronConfig.hour;
      cronConfig.day = cronConfig.day === '*' ? this.daysOfMonth[0].value : cronConfig.day;
      cronConfig.month = cronConfig.month === '*' ? this.months[0].value : cronConfig.month;
      cronConfig.weekday = '*';
    }
  }

  getWeekDayName(cronConfig: CronConfig) {
    const date = new Date(0);
    const firstSundayDate = date.getDate() + 3; // because we know date 0 was on Thursday...
    date.setDate(firstSundayDate + parseInt(cronConfig.weekday, 10));
    return formatDate(date, 'EEEE', this.translateService.currentLang);
  }

  getMonthDayName(cronConfig: CronConfig) {
    let name = '';
    this.daysOfMonth.forEach(item => {
      if (item.value === cronConfig.day) {
        name = item.label;
      }
    });

    return name;
  }

  getMonthName(cronConfig: CronConfig) {
    const date = new Date(0);
    date.setMonth(parseInt(cronConfig.month, 10) - 1);
    return formatDate(date, 'LLLL', this.translateService.currentLang);
  }
}
