import { Component, EventEmitter, Input, OnChanges, OnInit, Output } from '@angular/core';
import { Export, Schedule, EmitterPayload, ActionType } from './export-schedules.interface';
import { ReportsService } from './reports.service';
import { BsModalService, BsModalRef, ModalOptions } from 'ngx-bootstrap/modal';
import { ScheduleModalComponent } from './schedule-modal.component';
import { ConfirmModalComponent, Status, gettext, OptionsService } from '@c8y/ngx-components';
import { cloneDeep } from 'lodash-es';
import { CronService } from './cron.service';
import { IdReference } from '@c8y/client';
import { TranslateService } from '@ngx-translate/core';
import { UserService } from '@c8y/client';
@Component({
  selector: 'export-schedules',
  templateUrl: './export-schedules.component.html'
})
export class ExportSchedulesComponent implements OnInit, OnChanges {
  @Input() set exportId(exportId: IdReference) {
    this._exportId = exportId;
  }

  @Output() onSchedulesUpdate = new EventEmitter<Schedule[]>();

  exp: Export;
  scheduleList: Schedule[] = [];
  initialSchedule: Schedule = {
    timestamp: null,
    emailConfig: {
      to: [],
      cc: [],
      bcc: [],
      replyTo: '',
      text: '',
      subject: ''
    },
    cronConfig: {
      minute: '0',
      hour: '0',
      day: '1',
      month: '1',
      weekday: '?'
    }
  };
  buttonLabels: any;
  listClass: string = 'interact-list';
  loadingStatus: any;
  sortType: string;
  sortReverse: boolean = false;
  isOpen: any = {};
  isFlipped: boolean;
  isEditMenuOpen: boolean = false;
  modalRef: BsModalRef;
  currentUserEmail: string = '';
  hasRequiredRole: boolean = false;
  private _exportId: IdReference;
  private defaultExportEmailTemplate = this.translateService.instant(
    gettext(
      'File with exported data can be downloaded from {tenant-domain}/inventory/binaries/{binaryId}.'
    )
  );

  constructor(
    private reportsService: ReportsService,
    private bsModalService: BsModalService,
    public cronService: CronService,
    private translateService: TranslateService,
    private userService: UserService,
    private optionsService: OptionsService
  ) {
    this.loadingStatus = {
      inProgress: false,
      done: false,
      error: false
    };
  }

  async ngOnInit() {
    this.hasRequiredRole = await this.checkRole();
    this.getScheduleList(true);
    const currentUserEmail = await this.getCurrentUserEmail();
    this.initialSchedule.emailConfig.text = await this.optionsService.getTenantOption(
      'configuration',
      'export.data.mail.text',
      this.defaultExportEmailTemplate);
    this.initialSchedule.emailConfig.to = currentUserEmail;
    this.exp = await this.reportsService.getExport(this._exportId);
    this.initialSchedule.emailConfig.subject = this.translateService.instant(
      gettext('Export of "{{expName}}"'),
      { expName: this.exp.name }
    );
  }

  ngOnChanges() {
    this.translateButtonTitles();
  }

  translateButtonTitles() {
    this.buttonLabels = {
      edit: this.translateService.instant(gettext('Edit schedule')),
      editNoPermission: this.translateService.instant(gettext('Edit schedule - no permissions')),
      duplicate: this.translateService.instant(gettext('Duplicate schedule')),
      duplicateNoPermission: this.translateService.instant(gettext('Duplicate schedule - no permissions')),
      delete: this.translateService.instant(gettext('Delete schedule')),
      deleteNoPermission: this.translateService.instant(gettext('Delete schedule - no permissions'))
    };
  }

  async getCurrentUserEmail() {
    const { data } = await this.userService.current();
    return data && data.email ? [data.email] : [];
  }
  async checkRole() {
    const { data } = await this.userService.current();
    const role = 'ROLE_SCHEDULE_REPORT_ADMIN';
    const hasRole = this.userService.hasRole(data, role);
    return hasRole;
  }

  async getScheduleList(withProgress: boolean) {
    if (withProgress) {
      this.loadingStatus.inProgress = true;
    }
    this.scheduleList = await this.reportsService.getScheduleList(this._exportId);
    if (withProgress) {
      this.loadingStatus.inProgress = false;
    }
  }

  addSchedule() {
    this.openAddEditModal(this._exportId, this.initialSchedule, ActionType.CREATE);
  }

  editSchedule(schedule: Schedule, index: number, event: any) {
    if (this.hasRequiredRole) {
      event.preventDefault();
      this.openAddEditModal(this._exportId, schedule, ActionType.EDIT, index);
    }
  }

  duplicateSchedule(schedule: Schedule, event: any) {
    event.preventDefault();
    this.openAddEditModal(this._exportId, schedule, ActionType.DUPLICATE);
  }

  openAddEditModal(exportId: IdReference, schedule: Schedule, actionType: ActionType, index?: number) {
    const payload = { actionType, exportId, schedule: cloneDeep(schedule) };
    const modalOptions = { class: 'modal-sm', initialState: payload } as ModalOptions;
    this.modalRef = this.bsModalService.show(ScheduleModalComponent, modalOptions);
    this.modalRef.content.emitter.subscribe((load: EmitterPayload) =>
      this.getMessageFromModal(load, index)
    );
  }

  getMessageFromModal(payload: EmitterPayload, index?: number) {
    if (payload.success) {
      if (index !== undefined) {
        this.scheduleList[index] = payload.schedule;
      } else {
        this.scheduleList.push(payload.schedule);
      }
      this.onSchedulesUpdate.emit(this.scheduleList);
    }
  }

  removeSchedule(schedule: Schedule, index: number, event: any) {
    event.preventDefault();
    this.scheduleList.splice(index, 1);
    this.onSchedulesUpdate.emit(this.scheduleList);
  }
}
