import { Injectable } from '@angular/core';
import { Export, Schedule } from './export-schedules.interface';
import { AlertService } from '@c8y/ngx-components';
import { orderBy, isEqual, remove, some } from 'lodash-es';
import { InventoryService, IdReference } from '@c8y/client';
import { FetchClient, IFetchOptions } from '@c8y/client';
import { gettext } from '@c8y/ngx-components';

@Injectable()
export class ReportsService {
  ERROR_MESSAGES = {
    pattern_multiEmail: gettext('Invalid email addresses.'),
    pattern_singleEmail: gettext('Invalid email address.')
  };
  microserviceUrl: string;
  headers: any;

  constructor(
    private alertService: AlertService,
    private inventoryService: InventoryService,
    private client: FetchClient
  ) {
    this.microserviceUrl = '/service/reporting/schedule';
    this.headers = { 'Content-Type': 'application/json' };
  }

  async getExport(exportId: IdReference) {
    let exp: Export;
    const exportDetail = await this.inventoryService.detail(exportId);
    const { data, res } = exportDetail;
    if (res.status !== 200) {
      this.alertService.addServerFailure({ data, res });
    } else {
      exp = data ? ((data as unknown) as Export) : ({} as Export);
    }

    return exp;
  }

  async getScheduleList(exportId: IdReference) {
    const exp = await this.getExport(exportId);

    return this.extractScheduleListFromExport(exp);
  }

  extractScheduleListFromExport(exp: Export) {
    let scheduleList: Schedule[];
    if (exp) {
      scheduleList = exp.c8y_ScheduleConfiguration ? exp.c8y_ScheduleConfiguration : [];
    }
    return orderBy(scheduleList, ['timestamp'], ['desc']);
  }

  async addSchedule(schedule: Schedule, exportId: IdReference) {
    return await this.updateSchedules(exportId, [], [schedule]);
  }

  async updateSchedule(oldSchedule: Schedule, schedule: Schedule, exportId: IdReference) {
    return await this.updateSchedules(exportId, [oldSchedule], [schedule]);
  }

  async updateSchedules(
    exportId: IdReference,
    schedulesToRemove: Schedule[] = [],
    schedulesToAdd: Schedule[] = []
  ) {
    let success: boolean = false;
    const exp = await this.getExport(exportId);
    const schedules = this.extractScheduleListFromExport(exp);

    remove(schedules, (schedule: Schedule) =>
      some(schedulesToRemove, (scheduleToRemove: Schedule) => isEqual(schedule, scheduleToRemove))
    );
    schedules.push.apply(schedules, schedulesToAdd);
    exp.c8y_ScheduleConfiguration = schedules;
    const { data, res } = await this.inventoryService.update(exp);
    if (res.status === 200) {
      success = await this.reschedule(exportId);
    } else {
      this.alertService.addServerFailure({ data, res });
    }

    return success;
  }

  async reschedule(exportId: IdReference) {
    const options: IFetchOptions = {
      method: 'PUT',
      headers: this.headers
    };
    const rescheduling = await this.client.fetch(`${this.microserviceUrl}/${exportId}`, options);
    return rescheduling.status === 200;
  }

  async deleteSchedule(schedule: Schedule, exportId: IdReference) {
    return await this.updateSchedules(exportId, [schedule], []);
  }
}
